@use "../../variables" as var;
.WalletMenu {
  position: relative;
  width: fit-content;
  user-select: none;
}
.walletList {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  .walletItem {
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
    background-color: rgba(#333131, 0.5);
    padding: 10px;
    border-radius: 10px;

    cursor: pointer;
    img {
      width: 45px;
    }
    p {
      margin: 0;
      color: var.$color-content-text-yellow;
      font-size: 22px;
      font-weight: 700;
    }
    &:hover {
      opacity: 0.7;
    }
  }
}

.walletButton {
  font-size: 16px;
  line-height: 20px;
  height: fit-content;
  padding: 4px 8px;
  border-radius: 4px;
  color: white;
  // box-shadow: 5px 10px var.$color-primary-yellow;
  margin-left: 10px;
}
