@use "../../../variables" as var;

.CustomModal {
  :global(.ant-modal-content) {
    box-sizing: border-box;
    background: var.$color-content-background;
    border-radius: 24px;
    padding: 0;
  }
  :global(.ant-modal-body) {
    padding: 20px;
    color: #ffffff;
  }
}

.titleWrap {
  position: relative;
  display: flex;
  margin-bottom: 10px;
  // flex-direction: column;
  // align-items: flex-start;
  img {
    width: 15px;
    filter: invert(68%) sepia(64%) saturate(673%) hue-rotate(355deg)
      brightness(102%) contrast(91%);
    cursor: pointer;
  }
  h2 {
    width: 100%;
    text-align: center;
    color: var.$color-content-text-yellow;
    font-size: 24px;
    font-weight: 700;
    margin: 0;
  }
}
