@use "../../../variables" as var;
.Button {
  min-width: 120px;
  border-radius: 8px;
  padding: 15px 20px;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  text-transform: capitalize;
  background-color: var.$color-primary-yellow;
  color: #151414;
  user-select: none;
  cursor: pointer;
  box-shadow: rgba(var.$color-primary-yellow, 0.5) 0px 0px 20px 3px;

  &:hover {
    opacity: 0.8;
    box-shadow: rgba(var.$color-primary-yellow, 0.5) 0px 0px 20px 2px;
    scale: 0.95;
  }
  &.disabled {
    // pointer-events: none;
    cursor: not-allowed;
    opacity: 0.5;
    scale: unset;
    box-shadow: none;
  }
}
.blue {
  box-shadow: rgba(var.$color-primary-blue, 0.5) 0px 0px 20px 3px;

  &:hover {
    box-shadow: rgba(var.$color-primary-blue, 0.5) 0px 0px 20px 2px;
  }
  &.disabled {
    // pointer-events: none;
    cursor: not-allowed;
    opacity: 0.5;
    scale: unset;
    box-shadow: none;
  }
}
